let routes = [
  {
    path: '/view-proposal',
    component: ViewProposalPage,
  },
  {
    path: '/create-proposal/:ticket_id',
    component: CreateProposalPage,
  },
  {
    path: '/finalize-proposal/:client_id/:proposal_id/:user_type/:case_id',
    component: FinalizeProposalPage,
  },
  {
    path: '/create-benefit',
    component: CreateBenefit,
  },
  {
    path: "/approve-proposal/:client_id/:proposal_id/:ticket_id",
    component: ApproveProposal
  },
  {
    path: "/",
    component: Dashboard,
    exact: true
  },
  {
    path: "/home",
    component: HomePage,
  },
];
const prefix = '/underwriting';
routes.map(route => (route.path = `${prefix}${route.path}`));


export default function App() {

  const onLogout = () => {
    window.location.replace('../index.html#/');
  }

  return (
    <div>
      <ThemeProvider theme={theme}>
        <StylesProvider injectFirst>
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <Helmet titleTemplate="NEO - %s" defaultTitle="NEO">
              <meta name="description" content="NEO application" />
            </Helmet>
            <CssBaseline>
              <Components.UI.PageLayout history={history} page={"underwriting"} onLogout={onLogout}>
                <Switch>
                  {renderRoutes(routes)}
                  <Route path="" component={NotFoundPage} />
                </Switch>
              </Components.UI.PageLayout>
            </CssBaseline>
          </MuiPickersUtilsProvider>
        </StylesProvider>
      </ThemeProvider>
    </div>
  );
}